<template>
    <canvas :id="id" style="height: 100%; width: 100%;" height='auto' width='auto'/>
</template>

<script>
export default {
    props:{
        id:String,
        productionColor:String,
        consumptionColor:String,
        production:Array,        
        consumption:Array,
        stepNumber:Array,
    },

    watch:{
        production:function(){
            updateChart
            deep:true
        },

        consumption:function(){

            deep:true
        }
    },

    methods:{
        updateChart:function(){
            this.chart.data.datasets[0].data.pop()
            this.chart.data.datasets[1].data.pop()
        
            this.chart.data.datasets[0].data = this.production
            this.chart.data.datasets[0].data = this.consumption
            this.chart.data.labels = this.stepNumber

            this.chart.update()
        }
    },

    mounted(){
        const ctx = document.getElementById(this.id)
        this.chart = new Chart(ctx, {
            type: 'line',
            data:{
                labels: Array(24),
                datasets:[{
                        lineTension: 0,
                        data: Array(24),
                        label: 'Produced',
                        borderColor: "rgba(0,0,255,1)",
                        fill:false
                    },
                    {
                        lineTension: 0,
                        data: Array(24),
                        label: 'Produced',
                        borderColor: "#cd0000",
                        fill: false
                    }
                ]
            },
            options:{
                scales:{
                    yAxes:[{
                        ticks:{
                            beginAtZero:true,
                            callback:function(value,index,values){
                                return value + ' kW'
                            }
                        }
                    }]
                },
                legend:{
                    display:false,
                },
                animation:{
                    animateScale: false,
                    animateRotate: false,
                },
                title:{
                    display:false,
                    text:'(Energy) Consumption Vs Production'
                },
                scales:{
                    xAxes:[{
                        ticks:{
                            min:0,
                            max:24,
                            beginAtZero:true,
                        }
                    }]
                },

                defaultFontColor: '#1e1e1e',
                response: true,
                maintainAspectRatio: false,
                drawborder:false,
                cutoutPercentage: 70,
                rotation: Math.PI,
                circumference: 1 * Math.PI
            }
        })
    }

}
</script>

<style lang="scss" scoped>

</style>
